"use client";

import { useEffect, useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";

import { Product, adjustStock, loadProducts } from "@/lib/store";

export default function StockInPage() {
  const [products, setProducts] = useState<Product[]>([]);
  const [productId, setProductId] = useState("");
  const [qty, setQty] = useState<number>(1);

  useEffect(() => {
    setProducts(loadProducts());
  }, []);

  const refresh = () => setProducts(loadProducts());

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold">รับเข้า</h1>
          <p className="text-sm text-slate-500">เลือกรายการ + จำนวน แล้วเพิ่มสต็อก</p>
        </div>
      </div>

      <Card className="rounded-2xl">
        <CardContent className="space-y-3 p-4">
          <div className="text-sm font-medium">เลือกสินค้า</div>
          <select
            className="h-10 w-full rounded-xl border bg-white px-3 text-sm"
            value={productId}
            onChange={(e) => setProductId(e.target.value)}
          >
            <option value="">-- เลือกสินค้า --</option>
            {products.map((p) => (
              <option key={p.id} value={p.id}>
                {p.sku} - {p.name} (คงเหลือ {p.stock})
              </option>
            ))}
          </select>

          <div className="text-sm font-medium">จำนวนรับเข้า</div>
          <Input type="number" value={qty} onChange={(e) => setQty(Number(e.target.value))} />

          <Button
            className="rounded-xl"
            disabled={!productId || qty <= 0}
            onClick={() => {
              adjustStock(productId, qty);
              refresh();
              alert("รับเข้าเรียบร้อย ✅");
            }}
          >
            + รับเข้า
          </Button>
        </CardContent>
      </Card>
    </div>
  );
}
