export type Product = {
  id: string;
  sku: string;
  name: string;
  unit: string;
  stock: number;
  minStock: number;
};

const KEY = "stock_app_products_v1";

export function loadProducts(): Product[] {
  if (typeof window === "undefined") return [];
  const raw = localStorage.getItem(KEY);
  if (!raw) return [];
  try {
    return JSON.parse(raw) as Product[];
  } catch {
    return [];
  }
}

export function saveProducts(products: Product[]) {
  if (typeof window === "undefined") return;
  localStorage.setItem(KEY, JSON.stringify(products));
}

export function seedProductsIfEmpty() {
  const current = loadProducts();
  if (current.length > 0) return;

  const seeded: Product[] = [
    { id: crypto.randomUUID(), sku: "P001", name: "น้ำปลา", unit: "ขวด", stock: 24, minStock: 10 },
    { id: crypto.randomUUID(), sku: "P002", name: "ซีอิ๊วขาว", unit: "ขวด", stock: 2, minStock: 8 },
    { id: crypto.randomUUID(), sku: "P003", name: "น้ำมันพืช", unit: "ขวด", stock: 4, minStock: 6 },
  ];
  saveProducts(seeded);
}

export function addProduct(p: Omit<Product, "id" | "stock">) {
  const products = loadProducts();
  const next: Product = { id: crypto.randomUUID(), stock: 0, ...p };
  saveProducts([next, ...products]);
}

export function deleteProduct(id: string) {
  const products = loadProducts();
  saveProducts(products.filter((p) => p.id !== id));
}

export function adjustStock(id: string, delta: number) {
  const products = loadProducts();
  const next = products.map((p) =>
    p.id === id ? { ...p, stock: Math.max(0, p.stock + delta) } : p
  );
  saveProducts(next);
}
